.landing {
  height: 100vh;
  text-align: center;
}
.intro {
  position: absolute;
  left: 50vw;
  top: 50vh;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 90vw;
}
.intro>p {
 color: #8C8C8C;
}

.icon-scroll,
.icon-scroll:before {
  position: absolute;
  left: 48%;
  bottom: 20px;
}

@-webkit-keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }
}

@keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }
}

/* lg */
@media only screen and (min-width: 1440px) {

  .icon-scroll {
    width: 70px;
    height: 60px;
    -webkit-box-shadow: inset 0 0 0 1px #000000;
            box-shadow: inset 0 0 0 1px #000000;
    border-radius: 4px;
  }
  .icon-scroll:before {
    content: '';
    width: 8px;
    height: 8px;
    background: #000000;
    margin-left: -4px;
    top: 45px;
    border-radius: 4px;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: scroll;
            animation-name: scroll;
  }
}

/* md */
@media only screen and (max-width: 1439px) {
  .icon-scroll {
    width: 70px;
    height: 60px;
    -webkit-box-shadow: inset 0 0 0 1px #000000;
            box-shadow: inset 0 0 0 1px #000000;
    border-radius: 4px;
  }
  .icon-scroll:before {
    content: '';
    width: 8px;
    height: 8px;
    background: #000000;
    margin-left: -4px;
    top: 45px;
    border-radius: 4px;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: scroll;
            animation-name: scroll;
  }
}

/* sm */
@media only screen and (max-width: 1023px) {
  .row.landing {
    height: 100vh;
    text-align: center;
  }

  .icon-scroll {
    width: 50px;
    height: 70px;
    -webkit-box-shadow: inset 0 0 0 1px #000000;
            box-shadow: inset 0 0 0 1px #000000;
    border-radius: 4px;
  }
  .icon-scroll:before {
    content: '';
    width: 8px;
    height: 8px;
    background: #000000;
    margin-left: -4px;
    top: 45px;
    border-radius: 4px;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: scroll;
            animation-name: scroll;
  }
}

/* xs */
@media only screen and (max-width: 767px) {
    .landing {
        height: 100vh !important;
        text-align: center;
    }
    
    .keyFeature {
      border-radius: 0px !important;
      -webkit-box-shadow: none !important;
              box-shadow: none !important;
    }

    .icon-scroll {
      width: 40px;
      height: 70px;
      border-radius: 4px;
    }
    .icon-scroll:before {
      content: '';
      width: 8px;
      height: 8px;
      background: #000000;
      margin-left: -4px;
      top: 45px;
      border-radius: 4px;
      -webkit-animation-duration: 1.5s;
              animation-duration: 1.5s;
      -webkit-animation-iteration-count: infinite;
              animation-iteration-count: infinite;
      -webkit-animation-name: scroll;
              animation-name: scroll;
    }
}


